import Endpoint from '@/services/Endpoint'

export default {
  countryWeekend (payload) {
    return Endpoint().get('/s1/nucleus/basic/country/weekend', {
      params: payload
    })
  },
  createCountryWeekend (payload) {
    return Endpoint().post('/s1/nucleus/basic/country/weekend', payload)
  },
  modifyCountryWeekend (payload) {
    return Endpoint().patch('/s1/nucleus/basic/country/weekend', payload)
  },
  countryIbanFormat (payload) {
    return Endpoint().get('/s1/nucleus/basic/country/ibanFormat', {
      params: payload
    })
  },
  createCountryIbanFormat (payload) {
    return Endpoint().post('/s1/nucleus/basic/country/ibanFormat', payload)
  },
  modifyCountryIbanFormat (payload) {
    return Endpoint().patch('/s1/nucleus/basic/country/ibanFormat', payload)
  },
  countryHolidays (payload) {
    return Endpoint().get('/s1/nucleus/basic/country/holiday', {
      params: payload
    })
  },
  createCountryHoliday (payload) {
    return Endpoint().post('/s1/nucleus/basic/country/holiday', payload)
  },
  modifyCountryHoliday (payload) {
    return Endpoint().patch('/s1/nucleus/basic/country/holiday', payload)
  },
  deleteCountryHoliday (payload) {
    return Endpoint().delete('/s1/nucleus/basic/country/holiday', {
      params: payload
    })
  }
}
