<template>
  <div>
    <div class="alert-container" v-if="alertActive">
      <vs-alert :active.sync="alertActive" :color="alertVariant" closable icon-pack="feather" close-icon="icon-x" class="mb-5">
        <p>{{ alertMessage }}</p>
      </vs-alert>
    </div>

    <nv-card>
      <div class="flex flex-wrap-reverse items-center mb-base">
        <vs-button class="p-3 mr-4" @click="cancel" color="primary" type="filled" icon="fa-chevron-left" icon-pack="far">{{ $t('BACK') }}</vs-button>
        <vs-button class="p-3 mr-4" @click="modify" color="primary" type="filled" icon="fa-pen" icon-pack="far" v-if="$acl.check('isNucleusAll')">{{ $t('MODIFY') }}</vs-button>
      </div>
      <div class="row hover mb-base">
        <p class="info-row">
          <span>{{ $t('ID') }}:</span>
          <span>{{ country.geoId }}</span>
        </p>
        <p class="info-row">
          <span>{{ $t('COUNTRY_NAME') }}:</span>
          <span :class="disabledClass(country)">{{ country.geoName }}</span>
        </p>
        <p class="info-row">
          <span>{{ $t('NUMERIC_CODE') }}:</span>
          <span>{{ country.geoCodeNumeric }}</span>
        </p>
        <p class="info-row">
          <span>{{ $t('ALPHA2_CODE') }}:</span>
          <span>{{ country.geoCodeAlpha2 | placeholder('-') }}</span>
        </p>
        <p class="info-row">
          <span>{{ $t('ALPHA3_CODE') }}:</span>
          <span>{{ country.geoCodeAlpha3 | placeholder('-') }}</span>
        </p>
      </div>

      <vs-tabs>
        <vs-tab :label="$t('WEEKEND')">
          <div>
            <div class="nv-row" v-if="!hasWeekend">
              <div class="nv-col w-full">
                <vs-alert active="true" color="warning" class="mb-4">
                  <i class="far fa-exclamation-circle pr-1"></i> {{ $t('NO_DATA_FOUND') }}
                </vs-alert>
                <vs-button class="p-3" @click="createCountryWeekend" color="primary" type="filled" icon="fa-plus" icon-pack="far" v-if="$acl.check('isNucleusAll')">{{ $t('ADD') }}</vs-button>
              </div>
            </div>

            <div class="mt-4" v-else>
              <div class="nv-row mb-4">
                <div class="nv-col">
                  <vs-checkbox class="w-full" v-model="weekend.sunday">{{ $t('SUNDAY') }}</vs-checkbox>
                </div>
              </div>
              <div class="nv-row mb-4">
                <div class="nv-col">
                  <vs-checkbox class="w-full" v-model="weekend.monday">{{ $t('MONDAY') }}</vs-checkbox>
                </div>
              </div>
              <div class="nv-row mb-4">
                <div class="nv-col">
                  <vs-checkbox class="w-full" v-model="weekend.tuesday">{{ $t('TUESDAY') }}</vs-checkbox>
                </div>
              </div>
              <div class="nv-row mb-4">
                <div class="nv-col">
                  <vs-checkbox class="w-full" v-model="weekend.wednesday">{{ $t('WEDNESDAY') }}</vs-checkbox>
                </div>
              </div>
              <div class="nv-row mb-4">
                <div class="nv-col">
                  <vs-checkbox class="w-full" v-model="weekend.thursday">{{ $t('THURSDAY') }}</vs-checkbox>
                </div>
              </div>
              <div class="nv-row mb-4">
                <div class="nv-col">
                  <vs-checkbox class="w-full" v-model="weekend.friday">{{ $t('FRIDAY') }}</vs-checkbox>
                </div>
              </div>
              <div class="nv-row mb-4">
                <div class="nv-col">
                  <vs-checkbox class="w-full" v-model="weekend.saturday">{{ $t('SATURDAY') }}</vs-checkbox>
                </div>
              </div>

              <div class="nv-row">
                <div class="nv-col w-full">
                  <vs-button class="px-5 py-3 mr-2" @click="modifyCountryWeekend">{{ $t('SAVE') }}</vs-button>
                </div>
              </div>
            </div>
          </div>
        </vs-tab>

        <vs-tab :label="$t('IBAN_FORMATS')">
          <div>
            <div class="nv-row" v-if="!hasIbanFormat">
              <div class="nv-col w-full">
                <vs-alert active="true" color="warning" class="mb-4">
                  <i class="far fa-exclamation-circle pr-1"></i> {{ $t('NO_DATA_FOUND') }}
                </vs-alert>
                <vs-button class="p-3" @click="createCountryIbanFormat" color="primary" type="filled" icon="fa-plus" icon-pack="far" v-if="$acl.check('isNucleusAll')">{{ $t('ADD') }}</vs-button>
              </div>
            </div>

            <div v-else>
              <div class="nv-row mb-4">
                <div class="nv-col">
                  <label class="vs-input--label">{{ $t('IBAN_LENGTH') }}</label>
                  <vs-input-number v-model="ibanFormat.ibanLength" min="0"/>
                </div>
              </div>
              <div class="nv-row mb-4">
                <div class="nv-col sm:w-1/2 w-full">
                  <label class="vs-input--label">{{ $t('BBAN_FORMAT') }}</label>
                  <vs-input class="w-full" v-model="ibanFormat.bbanFormat"/>
                </div>
                <div class="nv-col sm:w-1/2 w-full">
                  <label class="vs-input--label">{{ $t('IBAN_FORMAT') }}</label>
                  <vs-input class="w-full" v-model="ibanFormat.ibanFormat"/>
                </div>
              </div>
              <div class="nv-row mb-4">
                <div class="nv-col sm:w-1/2 w-full">
                  <label class="vs-input--label">{{ $t('CHECK_DIGIT') }}</label>
                  <vs-select autocomplete class="w-full" :placeholder="$t('CHECK_DIGIT_TYPE')" v-model="ibanFormat.checkDigitTypeId">
                    <vs-select-item v-for="(enumeration, index) in checkDigitTypes" :key="index" :value="enumeration.enumId" :text="enumeration.description"/>
                  </vs-select>
                </div>
                <div class="nv-col sm:w-1/2 w-full">
                  <label class="vs-input--label">&nbsp;</label>
                  <vs-input class="w-full" v-model="ibanFormat.checkDigit" :disabled="ibanFormat.checkDigitTypeId !== 'CHECK_DIGIT_STATIC'"/>
                </div>
              </div>

              <div class="nv-row">
                <div class="nv-col w-full">
                  <vs-button class="px-5 py-3 mr-2" @click="modifyCountryIbanFormat">{{ $t('SAVE') }}</vs-button>
                </div>
              </div>
            </div>
          </div>
        </vs-tab>
        <vs-tab :label="$t('HOLIDAYS')">
          <country-holiday @show-alert="showAlert" :geoId="geoId"/>
        </vs-tab>
      </vs-tabs>
    </nv-card>
  </div>
</template>

<script>
import AlertMixin from '@/mixins/commons/AlertMixin'
import GeoService from '@/services/commons/GeoService'
import DisabledClassMixin from '@/mixins/commons/DisabledClassMixin'
import ListLayoutMixin from '@/mixins/commons/ListLayoutMixin'
import EnumerationService from '@/services/commons/EnumerationService'
import CountryService from '@/services/nucleus/CountryService'
import CountryHoliday from '@/views/nucleus/country/country-view/CountryHoliday'

export default {
  name: 'country-view',
  mixins: [AlertMixin, DisabledClassMixin, ListLayoutMixin],
  components: {
    CountryHoliday
  },
  data: function () {
    return {
      geoId: this.$route.params.id,
      country: {},
      weekend: {},
      hasWeekend: false,
      ibanFormat: {},
      hasIbanFormat: false,
      checkDigitTypes: []
    }
  },
  methods: {
    async load () {
      let self = this
      GeoService.geos({
        geoTypeEnumId: 'GEOT_COUNTRY',
        geoId: this.geoId
      }).then(function (response) {
        self.country = response.data.geoList[0] || {}
      })

      EnumerationService.enumerations({
        enumTypeId: 'CheckDigitType',
        orderByField: 'sequenceNum,description',
        pageNoLimit: true
      }).then(function (response) {
        self.checkDigitTypes = response.data.enumerationList || []
      })
    },
    loadCountryWeekend () {
      let self = this
      CountryService.countryWeekend({
        geoId: this.$route.params.id
      }).then(function (response) {
        self.weekend = response.data.weekend || {}
        if (Object.getOwnPropertyNames(self.weekend).length > 1) {
          self.weekend.sunday = self.weekend.sunday === 'Y'
          self.weekend.monday = self.weekend.monday === 'Y'
          self.weekend.tuesday = self.weekend.tuesday === 'Y'
          self.weekend.wednesday = self.weekend.wednesday === 'Y'
          self.weekend.thursday = self.weekend.thursday === 'Y'
          self.weekend.friday = self.weekend.friday === 'Y'
          self.weekend.saturday = self.weekend.saturday === 'Y'
          self.hasWeekend = true
        }
      })
    },
    createCountryWeekend () {
      let self = this
      CountryService.createCountryWeekend({
        geoId: this.$route.params.id,
        sunday: this.weekend.sunday ? 'Y' : 'N',
        monday: this.weekend.monday ? 'Y' : 'N',
        tuesday: this.weekend.tuesday ? 'Y' : 'N',
        wednesday: this.weekend.wednesday ? 'Y' : 'N',
        thursday: this.weekend.thursday ? 'Y' : 'N',
        friday: this.weekend.friday ? 'Y' : 'N',
        saturday: this.weekend.saturday ? 'Y' : 'N'
      }).then(function (response) {
        self.loadCountryWeekend()
      }).catch(function (error) {
        self.showDangerAlert(error.response.data.errors)
      })
    },
    modifyCountryWeekend () {
      let self = this
      CountryService.modifyCountryWeekend({
        geoId: this.$route.params.id,
        sunday: this.weekend.sunday ? 'Y' : 'N',
        monday: this.weekend.monday ? 'Y' : 'N',
        tuesday: this.weekend.tuesday ? 'Y' : 'N',
        wednesday: this.weekend.wednesday ? 'Y' : 'N',
        thursday: this.weekend.thursday ? 'Y' : 'N',
        friday: this.weekend.friday ? 'Y' : 'N',
        saturday: this.weekend.saturday ? 'Y' : 'N'
      }).then(function (response) {
        self.loadCountryWeekend()
        self.showSuccessAlert(response.data.messages)
      }).catch(function (error) {
        self.showDangerAlert(error.response.data.errors)
      })
    },
    loadCountryIbanFormat () {
      let self = this
      CountryService.countryIbanFormat({
        geoId: this.$route.params.id
      }).then(function (response) {
        self.ibanFormat = response.data.format || {}
        if (Object.getOwnPropertyNames(self.ibanFormat).length > 1) {
          self.hasIbanFormat = true
        }
      })
    },
    createCountryIbanFormat () {
      let self = this
      CountryService.createCountryIbanFormat({
        geoId: this.$route.params.id,
        ibanLength: this.ibanFormat.ibanLength,
        bbanFormat: this.ibanFormat.bbanFormat,
        ibanFormat: this.ibanFormat.ibanFormat,
        checkDigitTypeId: this.ibanFormat.checkDigitTypeId,
        checkDigit: this.ibanFormat.checkDigit
      }).then(function (response) {
        self.loadCountryIbanFormat()
      }).catch(function (error) {
        self.showDangerAlert(error.response.data.errors)
      })
    },
    modifyCountryIbanFormat () {
      let self = this
      CountryService.modifyCountryIbanFormat({
        geoId: this.$route.params.id,
        ibanLength: this.ibanFormat.ibanLength,
        bbanFormat: this.ibanFormat.bbanFormat,
        ibanFormat: this.ibanFormat.ibanFormat,
        checkDigitTypeId: this.ibanFormat.checkDigitTypeId,
        checkDigit: this.ibanFormat.checkDigit
      }).then(function (response) {
        self.loadCountryIbanFormat()
        self.showSuccessAlert(response.data.messages)
      }).catch(function (error) {
        self.showDangerAlert(error.response.data.errors)
      })
    },
    cancel () {
      this.$router.back()
    },
    modify () {
      this.$router.push({ name: 'ModifyCountry', params: { id: this.$route.params.id } })
    }
  },
  mounted () {
    this.load()
    this.loadCountryWeekend()
    this.loadCountryIbanFormat()
  }
}
</script>
